},
mounted(){
this.ArticleAjax()
if (this.$route.query.section) {
let e = 0 //防止报错
let section = this.$route.query.section
this.SwithTab(e,section)
}else{
this.SwitchBanner()
}
},
methods:{
SwitchBanner(){
if (this.TabIndex=='2') {
this.HeaderImg = this.HeaderHeadlineImg
}
if (this.TabIndex=='1') {
this.HeaderImg = this.HeaderPayattenImg
}
},
TurnToSpecail(){
this.$router.push({
path:"/specail"
})
},
SwithTab(e,section){
let index
if (section) {
index = section
}else{
index = e.target.getAttribute("data-index")
}
this.TabIndex=index
if (this.TabIndex=='1') {
this.TabShow=true

}else if(this.TabIndex=='2'){
this.TabShow=false

}
this.SwitchBanner()
},
ShowQrcode(item){
if (item.Qrcode==='') {
window.location=item.link
return
}
this.Qrcode=item.Qrcode
this.XDialogShow=true
},
ArticleAjax(){
this.LoadMoreShow=true
this.$http({
method:'GET',
url:ArticleUrl,
params:{p:p}
}).then((res)=>{
this.LoadMoreShow=false
if (!res.data.data.data.length) {
this.HasData=false
return
}
this.ArticleList=this.ArticleList.concat(res.data.data.data)
})
},
MoveStart(e){
this.StartPageY=e.touches[0].pageY
},
MoveEnd(e){
let distance=Math.abs(this.StartPageY-e.changedTouches[0].pageY)
if (distance>100) {
this.LoadMore()
}
},
LoadMore(){
if (this.HasData) {
p=p+1

this.ArticleAjax()


}
}
}
}



